.bai-test {
  max-width: 600px;
  margin: 0 auto;
}
.bai-test .question-time {
  justify-content: center;
  column-gap: 60px;
  row-gap: 60px;
}
.bai-test .question-time .itemt {
  position: relative;
  text-align: center;
  font-size: 20px;
  line-height: 1.2;
  color: var(--colormain);
}
.bai-test .question-time .itemt .number {
  font-size: 64px;
  font-weight: 700;
}
.bai-test .question-time .itemt.itemt + .itemt::before {
  content: "";
  border-left: 1px dashed #DBE2DF;
  position: absolute;
  left: -30px;
  height: 100%;
}
.bai-test .total-score {
  font-size: 64px;
  font-weight: 400;
  color: #656867;
  line-height: 1;
  text-align: center;
}
.bai-test .total-score span {
  font-weight: 700;
  color: var(--colormain);
}
.bai-test.in-progress {
  max-width: 760px;
}
.bai-test .question-item {
  border-radius: 8px;
  background: #fff;
  padding: 22px;
  box-shadow: 0 16px 32px -4px rgba(0, 0, 0, 0.1), 0 2px 4px 0 rgba(0, 0, 0, 0.04);
  margin-bottom: 30px;
  font-family: "Times New Roman";
  font-size: 18px;
  position: relative;
}
.bai-test .question-item .number-question {
  position: absolute;
  font-size: 12px;
  right: 10px;
  font-family: var(--font-main);
  color: #656867;
  top: 10px;
}
.bai-test .question-item .question {
  display: flex;
  padding-bottom: 22px;
  margin-bottom: 22px;
  border-bottom: 1px dashed #DBE2DF;
}
.bai-test .question-item .question .sentence {
  margin-right: 16px;
  color: #4E3CE6;
  font-weight: 700;
  flex-shrink: 0;
}
.bai-test .question-item .answer .flex {
  column-gap: 20px;
  row-gap: 20px;
}
.bai-test .question-item .answer .btn {
  border-radius: 4px;
  border: 2px solid #DBE2DF;
  font-size: 18px;
  color: #00110B;
  padding: 0px 20px;
  height: 56px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.bai-test .question-item .answer .btn .ic {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 22px;
  height: 22px;
  border: 2px solid #DBE2DF;
  border-radius: 50%;
  color: #acb5bf;
}
.bai-test .question-item .answer .btn .ic svg {
  width: 16px;
  height: 16px;
}
.bai-test .question-item .answer .btn.active {
  border: 2px solid var(--colormain);
  background: #F1F8F5;
}
.bai-test .question-item .answer .btn.active .ic {
  border: 2px solid var(--colormain);
  color: var(--colormain);
  background: #abd5c6;
}
.bai-test .question-item .answer .btn.suggest {
  border: 2px dashed #2D8755;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M8.79508 15.8749L4.62508 11.7049L3.20508 13.1149L8.79508 18.7049L20.7951 6.70492L19.3851 5.29492L8.79508 15.8749Z' fill='%232D8755'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 10px center;
}
.bai-test .question-item .answer .btn.suggest .ic {
  display: none;
}
.bai-test .question-item .answer .btn.wrong {
  border: 2px solid #D93025;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='25' height='24' viewBox='0 0 25 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M19.5 6.41L18.09 5L12.5 10.59L6.91 5L5.5 6.41L11.09 12L5.5 17.59L6.91 19L12.5 13.41L18.09 19L19.5 17.59L13.91 12L19.5 6.41Z' fill='%23D93025'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 10px center;
}
.bai-test .question-item .answer .btn.wrong .ic {
  display: none;
}
.bai-test .question-item .answer .gap_30 {
  column-gap: 60px !important;
  row-gap: 20px !important;
}
.bai-test .question-item .answer .itemt {
  display: flex;
  align-items: center;
}
.bai-test .question-item .answer .itemt .text {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  border-radius: 4px;
  border: 1px solid #011D12;
  flex-shrink: 0;
  margin-right: 20px;
}
.bai-test .notifi-missing-sentence {
  background: #fff;
  border-radius: 24px;
  padding: 16px;
  max-width: 650px;
  margin: 0 auto;
}
@media (max-width: 600px) {
  .bai-test .grid-button .next {
    grid-column: 2 span;
  }
  .bai-test .question-item {
    margin-bottom: 16px;
    padding: 22px 16px;
  }
  .bai-test .question-item .answer .gap_30 {
    grid-template-columns: repeat(1, 1fr);
  }
}

.statistical-progress {
  justify-content: center;
  column-gap: 40px;
  row-gap: 40px;
}
.statistical-progress .itemt {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: start;
  row-gap: 8px;
  --color: #09C;
}
.statistical-progress .itemt .top,
.statistical-progress .itemt .bottom {
  width: 100%;
}
.statistical-progress .itemt .top {
  font-size: 16px;
  align-items: center;
  justify-content: center;
  color: var(--color);
  column-gap: 5px !important;
  row-gap: 5px !important;
}
.statistical-progress .itemt .top svg {
  width: 22px;
  height: 22px;
}
.statistical-progress .itemt .bottom {
  font-weight: 300;
}
.statistical-progress .itemt .progress-circle {
  position: relative;
  width: 100px;
  height: 100px;
}
.statistical-progress .itemt .progress-ring {
  transform: rotate(-90deg);
}
.statistical-progress .itemt .progress-ring-bg {
  fill: none;
  stroke: #dfe6e9;
  stroke-width: 7;
}
.statistical-progress .itemt .progress-ring-circle {
  fill: none;
  stroke: var(--color);
  stroke-width: 7;
  stroke-linecap: round;
  stroke-dasharray: 339.292;
  stroke-dashoffset: 0;
  transition: stroke-dashoffset 1s ease;
}
.statistical-progress .itemt .progress-circle-inner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.statistical-progress .itemt .percentage {
  font-size: 24px;
  font-weight: bold;
  color: var(--color);
}
.statistical-progress .itemt .fraction {
  font-size: 14px;
  color: #ABB0AE;
}
.statistical-progress .itemt .fraction i {
  font-style: normal;
  color: #656867;
}
.statistical-progress .itemt .fraction em {
  color: var(--color);
  font-style: normal;
}
.statistical-progress .itemt:nth-child(2) {
  --color: #A674F8;
}
.statistical-progress .itemt:nth-child(3) {
  --color: #F9A826;
}
.statistical-progress .itemt:nth-child(4) {
  --color: #FF5E57;
}
.statistical-progress.grid__4 {
  column-gap: 20px;
  row-gap: 20px;
}
@media (max-width: 600px) {
  .statistical-progress {
    column-gap: 10px;
    row-gap: 20px;
  }
  .statistical-progress .itemt .top {
    font-size: 14px;
    column-gap: 5px !important;
    row-gap: 5px !important;
  }
  .statistical-progress .itemt .top svg {
    width: 20px;
    height: 20px;
  }
  .statistical-progress .itemt .bottom {
    font-size: 12px;
  }
}

.history-test ul {
  margin-bottom: 0;
  padding: 16px;
  border: 1px solid #DBE2DF;
  border-radius: 8px;
}
.history-test ul li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  color: #656867;
}
.history-test ul li:nth-child(n+2) {
  margin-top: 16px;
  padding-top: 16px;
  border-top: 1px dashed #DBE2DF;
}
.history-test ul li .gap_30 {
  align-items: center;
}
.history-test ul li .gap_30 > div {
  position: relative;
}
.history-test ul li .gap_30 > div:nth-child(n+2)::before {
  content: "";
  background: #DBE2DF;
  position: absolute;
  width: 1px;
  height: 100%;
  left: -15px;
}
.history-test ul li .gap_30 .ic {
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  background: #D93025;
  border-radius: 4px;
}
.history-test ul li .gap_30 .ic.success {
  background: #2D8755;
}
.history-test ul li .point span {
  color: #023D28;
}
.history-test ul li .review {
  border: 1px solid #656867;
  border-radius: 4px;
  padding: 5px 8px;
}
.history-test ul.full {
  border: 0;
  padding: 0;
}
.history-test ul.full li {
  font-size: 14px;
}
.history-test ul.full li .gap_30 .ic {
  width: 125px;
  gap: 5px;
  padding: 0 5px;
}
@media (max-width: 1480px) {
  .history-test ul.full li .flexbox.gap_16 {
    display: none;
  }
}

.progress-step {
  position: relative;
  height: 3px;
  background: #BAE0D3;
  border-radius: 50px;
  margin: -30px -30px 30px -30px;
}
.progress-step .step {
  position: absolute;
  background: var(--colormain);
  height: 3px;
  border-radius: 50px;
  left: 0;
  top: 0px;
  transition: 0.2s all;
}
@media (max-width: 1480px) {
  .progress-step {
    margin: -20px -20px 20px -20px;
  }
}
.test-time {
  width: 400px;
}
.test-time .countdown {
  justify-content: center;
  gap: 30px;
}
.test-time .countdown .item_countdown {
  color: #011D12;
  line-height: 1.2;
}
.test-time .countdown .item_countdown::after {
  font-size: 32px;
  top: 9px;
  right: -18px;
}
.test-time .countdown .item_countdown .item_txt span {
  font-size: 32px;
}
.test-time .countdown .item_countdown .item_txt .text {
  font-size: 10px;
  color: #ABB0AE;
}
.test-time .countdown.almost-time .item_countdown {
  color: #FF5E57;
}
@media (max-width: 991px) {
  .test-time .hidden-mobile {
    display: none;
  }
}

.question-section {
  background: #fff;
}
.question-section .close-question {
  display: none;
}
.question-section .btn {
  display: flex;
  font-size: 16px;
  width: 100%;
  justify-content: center;
  align-items: center;
  height: 40px;
  padding: 0 5px;
  color: #656867;
  border: 1px solid #DBE2DF;
  border-radius: 4px;
}
.question-section .btn.active {
  color: #fff;
  border: 1px solid var(--colormain);
  background: var(--colormain);
}
.question-section .section {
  border-bottom: 1px solid #DBE2DF;
  margin-bottom: 12px;
  padding-bottom: 12px;
  display: flex;
  column-gap: 12px;
  row-gap: 12px;
  width: 100%;
}
.question-section .section .prev,
.question-section .section .next {
  width: 30px;
}
.question-section .section .prev svg,
.question-section .section .next svg {
  width: 14px;
  height: 14px;
}
.question-section .list .grid {
  column-gap: 12px;
  row-gap: 12px;
}
.question-section .list-section .grid {
  column-gap: 12px;
  row-gap: 12px;
}
.question-section .list-section .btn {
  gap: 15px;
}
.question-section .list-section .btn.wrong {
  background: #D93025;
  border: 1px solid #D93025;
  color: #fff;
}
.question-section .list-section .btn.success {
  background: #2D8755;
  border: 1px solid #2D8755;
  color: #fff;
}
.question-section .list-section .itemt {
  display: flex;
  font-size: 16px;
  width: 100%;
  justify-content: center;
  align-items: center;
  height: 40px;
  padding: 0 12px;
  column-gap: 12px;
  color: #656867;
  border: 1px solid #DBE2DF;
  border-radius: 4px;
}
.question-section .list-section .itemt .btn {
  height: 26px;
}
.question-section .list-section .itemt .grid {
  column-gap: 4px;
  row-gap: 4px;
}
@media (max-width: 991px) {
  .question-section {
    position: fixed;
    background: #fff;
    bottom: 71px;
    -webkit-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
    z-index: 1001;
    padding: 46px 24px 16px 24px !important;
    width: 100%;
    border-bottom: 1px solid #dbe2df;
    box-shadow: rgba(0, 0, 0, 0.1) 0 5px 20px 10px;
    opacity: 0;
    visibility: hidden;
  }
  .question-section .scroll,
  .question-section .list {
    max-height: calc(100dvh - 195px);
    overflow-x: auto;
  }
  .question-section .padding-line-bottom {
    padding: 0 0 16px 0;
    margin-bottom: 16px;
  }
  .question-section .padding-line-bottom:last-child {
    margin-bottom: 0;
    border-bottom: 0;
  }
  .question-section .section {
    display: none;
  }
  .question-section .close-question {
    display: block;
    position: absolute;
    right: 20px;
    top: 10px;
    font-size: 0;
  }
  .question-section.show {
    opacity: 1;
    visibility: visible;
  }
  .question-section .grid__5 {
    grid-template-columns: repeat(5, 1fr);
  }
  .question-section .list-section .grid__3 {
    grid-template-columns: repeat(3, 1fr);
  }
  .question-section .list-section .grid__4 {
    grid-template-columns: repeat(4, 1fr);
  }
}

.sticky-test-time {
  display: none;
  position: sticky;
  top: 0;
  background: #fff;
  z-index: 1001;
}
.sticky-test-time .test-time {
  padding: 0 15px;
  width: 100%;
  height: 60px;
}
.sticky-test-time .test-time .countdown .item_countdown::after {
  font-size: 20px;
  top: 17px;
}
.sticky-test-time .test-time .countdown .item_countdown .item_txt span {
  font-size: 20px;
}
.sticky-test-time .test-time .btn-default {
  height: 40px;
  padding: 0 10px;
}
.sticky-test-time .progress-step {
  margin: 0;
}
@media (max-width: 991px) {
  .sticky-test-time {
    display: block;
  }
}

.question-section-mobile {
  display: none;
  padding: 16px 24px;
  background: #fff;
  position: fixed;
  bottom: 0;
  z-index: 1001;
  left: 0;
  right: 0;
  box-shadow: rgba(0, 0, 0, 0.1) 0 5px 20px 10px;
}
@media (max-width: 991px) {
  .question-section-mobile {
    display: flex;
    column-gap: 12px;
    row-gap: 12px;
    width: 100%;
    align-items: center;
  }
  .question-section-mobile .btn {
    display: flex;
    font-size: 16px;
    width: 100%;
    justify-content: center;
    align-items: center;
    height: 40px;
    padding: 0 5px;
    color: #656867;
    border: 1px solid #DBE2DF;
    border-radius: 4px;
    outline: 0;
  }
  .question-section-mobile .btn.active, .question-section-mobile .btn:focus {
    color: #fff;
    border: 1px solid var(--colormain);
    background: var(--colormain);
    box-shadow: none;
  }
  .question-section-mobile .prev,
  .question-section-mobile .next {
    width: 30px;
  }
  .question-section-mobile .prev svg,
  .question-section-mobile .next svg {
    width: 14px;
    height: 14px;
  }
}

.box-log {
  background: #fff;
  border-radius: 4px;
  padding: 16px;
  border: 1px solid #D93025;
  display: flex;
}
.box-log h3 {
  font-size: 24px;
  color: #D93025;
  margin-bottom: 0;
}
@media (max-width: 600px) {
  .box-log {
    flex-wrap: wrap;
  }
  .box-log h3 {
    font-size: 20px;
  }
}

.form-login2 .form-group {
  max-width: 500px;
  display: flex;
  align-items: center;
}
.form-login2 .form-group label {
  color: #4E565C;
  margin-bottom: 0;
  flex-shrink: 0;
  width: 150px;
}
.form-login2 .form-group .relative {
  width: 100%;
}

.step-button {
  display: flex;
  align-items: center;
  font-size: 16px;
  color: #034C31;
}
.step-button .step {
  background: #034C31;
  color: #fff;
  padding: 0 16px;
  height: 100%;
  border-radius: 8px 0 0 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.step-button .content {
  padding: 8px 16px;
  height: 100%;
  border-radius: 0 8px 8px 0;
  border: 1px solid #034C31;
}
.step-button .content img {
  width: 25px;
}
.step-button.disable {
  color: #ABB0AE;
}
.step-button.disable .step {
  background: #ABB0AE;
}
.step-button.disable .content {
  border: 1px solid #ABB0AE;
}

.flex .step-button {
  position: relative;
}
.flex .step-button:nth-child(n+2)::before {
  content: "";
  background: #ABB0AE;
  position: absolute;
  width: 30px;
  height: 1px;
  left: -30px;
}
.flex .step-button.success::before {
  background: #034C31;
}
@media (max-width: 991px) {
  .flex {
    column-gap: 15px !important;
    row-gap: 15px !important;
  }
  .flex .step-button::before {
    display: none;
  }
}

.box-traucu {
  border-radius: 4px;
  border: 1px solid #D93025;
  background: #FEF6E9;
  padding: 16px;
}
.box-traucu .flexbox {
  align-items: center;
}
.box-traucu .flexbox .id-bai,
.box-traucu .flexbox .btn-default {
  flex-shrink: 0;
}
.box-traucu .flexbox .form-control {
  height: 42px;
}
.box-traucu .col-md-7 {
  padding-right: 30px;
  border-right: 1px dashed rgba(107, 119, 154, 0.15);
