@media (max-width: 1025px) {
  .box-traucu .col-md-7 {
    padding-right: 15px;
  }
}
@media (max-width: 600px) {
  .box-traucu .text-center {
    text-align: left;
  }
  .box-traucu .flexbox {
    flex-wrap: wrap;
    column-gap: 10px !important;
    row-gap: 10px !important;
    padding-bottom: 16px;
    border-bottom: 2px dashed rgba(107, 119, 154, 0.15);
  }
  .box-traucu .flexbox .id-bai {
    width: 100%;
  }
  .box-traucu .flexbox .form-control {
    width: calc(100% - 150px);
  }
  .box-traucu .col-md-7 {
    border-right: 0;
  }
}